.users-container {
  display: flex;
  flex-direction: column;
  background: var(--background2);
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  padding: 0 10px 15px 10px;
  border-radius: 10px; }
  .users-container h1 {
    margin: 0;
    font-size: 1.3em;
    text-align: center;
    vertical-align: center;
    color: var(--stroke); }
  .users-container .user-container {
    display: flex;
    background: var(--background1);
    border-radius: 10px;
    padding: 3px 0;
    margin: 5px 10px;
    cursor: pointer; }
    .users-container .user-container .userimg {
      height: 60px;
      margin: 0 5px; }
      .users-container .user-container .userimg img {
        height: 100%; }
    .users-container .user-container .profile {
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      justify-content: center; }
      .users-container .user-container .profile .name {
        color: var(--stroke); }
        .users-container .user-container .profile .name:hover {
          text-decoration: underline; }
      .users-container .user-container .profile span {
        color: var(--paragraphy); }

.posts-container {
  display: flex;
  flex-direction: column;
  background-color: var(--background2);
  border-radius: 10px;
  padding: 10px 0;
  overflow-y: scroll;
  max-height: 80vh; }
  .posts-container .post-container {
    background: var(--background1);
    border-radius: 5px;
    margin: 0 15px 15px 15px;
    padding: 10px;
    vertical-align: center; }
    .posts-container .post-container .post-title {
      font-size: 40px;
      color: var(--stroke);
      font-weight: 600;
      background: var(--background);
      font-size: 22px;
      display: flex;
      justify-content: space-between; }
      .posts-container .post-container .post-title .title:hover {
        cursor: pointer;
        text-decoration: underline; }
      .posts-container .post-container .post-title .date {
        font-size: 15px;
        color: var(--paragraphy); }
    .posts-container .post-container .post-body {
      font-size: 18px;
      color: var(--paragraphy); }
    .posts-container .post-container .post-footer {
      font-weight: 600;
      color: var(--stroke);
      text-align: right;
      padding-right: 2px;
      font-size: 18px; }
      .posts-container .post-container .post-footer span {
        cursor: pointer; }
        .posts-container .post-container .post-footer span:hover {
          text-decoration: underline; }

.homepage-grid {
  margin: 0 30px;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 20px;
  grid-template-areas: "righttop main main main" "rightbottom main main main"; }
  .homepage-grid .users-grid {
    grid-area: righttop;
    min-width: 0;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content; }
  .homepage-grid .comments-grid {
    grid-area: rightbottom;
    min-height: 0;
    min-width: 0;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content; }
  .homepage-grid .posts-grid {
    grid-area: main; }

@media only screen and (max-width: 980px) {
  .homepage-grid {
    grid-template-areas: "main main main main"; }
    .homepage-grid .comments-grid {
      display: none; }
    .homepage-grid .users-grid {
      display: none; } }

